/**
 * 在元素之外点击时触发回调函数
 * @param {HTMLElement} element 要监听的元素
 * @param {Function} callback 点击元素之外时要执行的回调函数
 * @returns {Function} 用于移除事件监听器的函数
 */
export function clickOutside(element, callback) {
  // 定义事件处理函数
  const handleOutsideClick = event => {
    // 判断当前点击的元素是否在要监听的元素内部
    if (!element.contains(event.target)) {
      // 如果不在，则执行回调函数
      callback(event);
    }
  };
  // 使用 document.addEventListener 添加 click 事件监听器
  document.addEventListener('click', handleOutsideClick);
  // 返回一个函数，用于移除事件监听器
  return () => {
    document.removeEventListener('click', handleOutsideClick);
  };
}